import React from 'react'
import { Link } from 'react-router'
import { prefixLink } from 'gatsby-helpers'
import { config } from 'config'
import { MorphReplace } from 'react-svg-morph';
import Checked from '../SVG/Checked';
import CheckBox from '../SVG/CheckBox';

import './style.scss'
import coriolanLogo from './coriolanLogo.svg'

class HomePage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            checked: false
        };
    }
    toggleChecked() {
        this.setState({checked: !this.state.checked});
    }
    render() {
        const {route} = this.props
        const page = route.page.data

        return (
            <div className='homepage'>
                <div dangerouslySetInnerHTML={{ __html: page.body}} />

                <div onClick={this.toggleChecked.bind(this)}>
                    <MorphReplace width={500} height={500}>
                        {this.state.checked ? <Checked key="checked" /> : <CheckBox key="checkbox" />}
                    </MorphReplace>
                </div>

            </div>
        );
    }
}

HomePage.propTypes = {
    page: React.PropTypes.object,
}

export default HomePage



